/* eslint-disable */
import axios from 'axios';
import { showAlert } from './alerts';

export const updateUserInfor = async (userId, name, page, rowsPerPage, searchQuery) => {
  try {
    const res = await axios({
      method: 'PATCH',
      url: `/api/v1/users/${userId}`,
      data: {
        name
      }
    });

    if (res.data.status === 'success') {
      showAlert('success', 'Cp nht thng tin ngi dng thnh cng!');
      $.get(window.location.href, { page: page + 1, rowsPerPage: rowsPerPage, search: searchQuery }, function(data) {
        $('#userTable').html(data.userTableHtml);
        $('#paginationWrapper').html(data.paginationHtml);
      });
    }
  } catch (err) {
    showAlert('error', `Li cp nht thng tin ngi dng: ${err.response.data.message}`);
  }
};

export const updateUserPassword = async (userId, password, passwordConfirm, page, rowsPerPage, searchQuery) => {
  try {
    console.log(userId, password, passwordConfirm)
    const res = await axios({
      method: 'PATCH',
      url: `/api/v1/users/${userId}/updateUserPassword`,
      data: {
        password,
        passwordConfirm
      }
    });

    if (res.data.status === 'success') {
      showAlert('success', 'Cp nht mt khu ngi dng thnh cng!');
      $.get(window.location.href, { page: page + 1, rowsPerPage: rowsPerPage, search: searchQuery }, function(data) {
        $('#userTable').html(data.userTableHtml);
        $('#paginationWrapper').html(data.paginationHtml);
      });
    }
  } catch (err) {
    showAlert('error', `Li cp nht mt khu ngi dng: ${err.response.data.message}`);
  }
};

